<template>
  <div style="height:100%">
    <el-row>
      <el-col :span="14">
        <el-button type="primary" @click="setSelectFun()"></el-button>
        <el-button type="primary" @click="getSelectFun()"></el-button>
        <el-button type="primary" @click="addFun()"></el-button>
        <el-button type="primary" @click="addFun(2,keys)"></el-button>
        <el-button type="primary" @click="deleteFun"></el-button>
        <el-button type="primary" @click="deleteFun(2)"></el-button>
        <el-button type="primary" @click="editFun"></el-button>
      </el-col>
      <el-col :span="10">
        <el-col :span="15">
          <el-input v-model="userInput" placeholder="" ></el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="search"></el-button>
        </el-col>
      </el-col>
    </el-row>
    <el-table
      :data="tableData4"
      ref="singleTable"
      highlight-current-row
     @cell-click="onRowSelected"
     @select="onCheckRows"
      :summary-method="getSummaries"
      show-summary
      style="width: 100%"
      max-height="500">
      <el-table-column
        label=""
        type="index">
      </el-table-column>
      <el-table-column
      type="selection"
      width="55">
      </el-table-column>
      <el-table-column
        prop="date"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="province"
        label="">
      </el-table-column>
      <el-table-column
        prop="name"
        label="">
      </el-table-column>
      <el-table-column
        prop="city"
        label="">
      </el-table-column>
      <el-table-column
        prop="address"
        label="">
      </el-table-column>
      <el-table-column
        prop="zip"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="120">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="deleteRow(scope.$index, tableData4)"
            type="text"
            size="small">
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-table
      :data="childTableData"
      ref="singleTable"
      highlight-current-row
      style="width: 100%"
      max-height="500"
      empty-text="">
      <el-table-column
        label=""
        type="index">
      </el-table-column>
      <el-table-column
      type="selection"
      width="55">
      </el-table-column>
      <el-table-column
        prop="id"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="age"
        label=""
        width="120">
      </el-table-column>
    </el-table>
  </div>  
</template>
<script>
import Axios from 'axios'
  export default {
    methods: {
      getSummaries(param) {
        const { columns, data } = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] += ' ';
          } else {
            sums[index] = '';
          }
        });
        return sums;
      },
      setSelectFun(){
        this.$refs.singleTable.setCurrentRow(this.tableData4[1]);
      },
      getSelectFun(){
        alert(this.selectRow.name);
      },
      onCheckRows(selection, row){
        this.checkRows = selection;
      },
      // check
      onRowSelected(row, column, cell, event) {
        //
        this.selectRow = row;
        this.setChildGridData(row);
      },
      deleteRow(index, rows) {
        rows.splice(index, 1);
      },
      deleteFun(index) {
        //id   tableData4
        let delIndex = index || 1;
        this.tableData4.splice(delIndex, 1);
      },
      editFun(){
        this.$router.push({ name: 'edit', params: { selectRowId: this.selectRow.id }})
      },
      addFun(){
        for(var k = 0; k < 1200; k++){
          var data = {
            id: 9999999,
            date: '2016-05-03',
            name: '',
            province: '',
            city: '',
            address: ' 1518 ',
            zip: 200333
          };
          this.tableData4.push(data);
        }
      },
      setChildGridData(row){
        this.childTableData = this.childGridData[row.id];
      },
      search(){
        alert("");
        Axios.get('/api/data')
        .then(function(response){
          console.log(response);
        })
        .catch(function(err){
          console.log(err);
        });
        // :1
        //           forset
        //      2
        //           set
        //      3
        //           
        // if(this.userInput !== ""){
        //   for( let m = 0; m < this.tableData4.length; m++){
        //     for( let n in this.tableData4[m]){
        //       if( this.tableData4[m][n] == this.userInput){
        //         this.tableData4[m][n] = `span${ this.userInput }`;
        //       }
        //     }
        //   }
        // }
      }
    },
    mounted:function(){
      let that = this;
      //: 1 
            //2,
            //3tableData4
      Axios.get('/api/data')
      .then(function(res){
          console.log(res);
          that.tableData4 = res.data.initData;
          that.childGridData = res.data.childData;
      })
    },
    data() {
      return {
        userInput:'',
        selectRow:[],
        checkRows:[],
        childTableData:[],
        tableData4: [],
        childGridData:{}
      }
    }
  }
</script>


