<template>
	<div>
		<p @click="showName">this is show a demo : {{ name }}</p>
		<el-button></el-button>
		<el-button type="primary"></el-button>
		<el-button type="success"></el-button>
		<el-button type="info"></el-button>
		<el-button type="warning"></el-button>
		<el-button type="danger"></el-button>
	</div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex'

export default {
	computed: mapGetters([
		'name'
	]),
	methods: mapActions([
     'showName'
  ]),
}

</script>

<style scoped>
	p {
		font-size: 20px;
		color: blue;
	}
</style>


