<template>
	<el-row>
		<el-col :span="6">
			<el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
			  <el-form-item label="" prop="date">
			    <el-date-picker type="date" placeholder="" v-model="ruleForm2.date" style="width: 100%;"></el-date-picker>
			  </el-form-item>
        <el-form-item label="" prop="province">
          <el-input v-model.number="ruleForm2.province"></el-input>
        </el-form-item>
        <el-form-item 
        label="" 
        prop="name"
        >
          <el-input v-model.number="ruleForm2.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="city">
          <el-input v-model.number="ruleForm2.city"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address"
        :rules="[
          { required: true, message: ''}
        ]"
        >
          <el-input v-model.number="ruleForm2.address"></el-input>
        </el-form-item>
        <el-form-item label="" prop="zip"
        :rules="[
          { required: true, message: ''},
          { type: 'number', message: ''}
        ]"
        >
          <el-input v-model.number="ruleForm2.zip"></el-input>
        </el-form-item>
			  <el-form-item>
			    <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
			    <el-button @click="resetForm('ruleForm2')"></el-button>
			  </el-form-item>
			</el-form>
		</el-col> 
	</el-row>
</template>
<script>
  export default {
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //1
                  //2 
                  //3
            alert('console');
            let edited_data = {
              date: this.ruleForm2.date,
              province: this.ruleForm2.province,
              name: this.ruleForm2.name,
              city: this.ruleForm2.city,
              address: this.ruleForm2.address,
              zip: this.ruleForm2.zip
            }
            console.log(edited_data);
            this.$router.push({ name: 'elTable'})
          } else {
            alert('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    mounted:function(){
      //: 1id
            //2
            //3form
      var select_row_id = this.$route.params.selectRowId;
      for(let i of this.tableData4){
        if(i.id == select_row_id){
            this.ruleForm2.date = i.date;
            this.ruleForm2.province = i.province;
            this.ruleForm2.name = i.name;
            this.ruleForm2.city = i.city;
            this.ruleForm2.address = i.address;
            this.ruleForm2.zip = i.zip;
        }
      }
    },
    data() {
      var checkProvince = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        } else {
          callback();
        }
      };
      var checkName = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        } else {
          if(value.length > 2){
            return callback(new Error('2'));
          } else {
            callback();
          }
        }
      };
      return {
        ruleForm2: {
          date: '',
          province: '',
          name: '',
          city: '',
          address: '',
          zip: ''
        },
        rules2: {
          province: [
            { validator: checkProvince, trigger: 'blur' }
          ],
          name: [
            { validator: checkName, trigger: 'blur' }
          ]
        },
        tableData4: [{
          id: 111,
          date: '2016-05-03',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          id: 222,
          date: '2016-05-02',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          id: 333,
          date: '2016-05-04',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          id: 444,
          date: '2016-05-01',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }]
      }
    }
  }
</script>


